import { FaRegPlusSquare } from "react-icons/fa"
import { v4 as uuidv4 } from 'uuid'

const Form = ({ input, setInput, todos, setTodos }) => {
    const inputing = e => {
        setInput(e.target.value)
    }
    const submiting = e => {
        e.preventDefault()
        setTodos([
            ...todos,
            {text: input, completed: false, id: uuidv4()}
        ])
        setInput('')
    }

    return (
        <form>
            {<input value={input} onChange={inputing} type="text" />}
            <button onClick={submiting} type="submit"><FaRegPlusSquare /></button>
        </form>
    )
}

export default Form;