import Todo from "./Todo"

const TodoList = ({ todos, setTodos }) => {
    return (
        <div>
            <ul>
                {todos.map(todo => (
                    <Todo todo={todo} todos={todos} setTodos={setTodos} key={todo.id} text={todo.text} />
                ))}
            </ul>
        </div>
    )
}
export default TodoList